.main-banner__animation-wrap {
  position: relative;
  max-width: 1335px;
  margin: 1em auto;
  transform-style: preserve-3d;
  perspective: 400px;
  text-align: right;
}

.animation-letter.animation-letter-gray {
  color: rgba(255, 255, 255, 0.42);
}

.animation-letter {
  color: #000000;
}

.animation-letter,
.animation-space {
  font-family: "TrolaLatCyr", sans-serif;
  display: inline-block;
  font-size: 240px;
  text-transform: uppercase;
  position: relative;
  transform-style: preserve-3d;
}

.animation-space {
  width: .3em;
}

@keyframes anim1 {
  0% {
    transform: translate3d(1111px, 62px, 0) rotate(0deg);
    opacity: 1;
  }

  100% {
    transform: translate3d(0, 0, 0) rotate(-360deg);
    opacity: 1;
  }
}

.animation-letter:nth-child(1) {
  animation: anim1 3s;
  animation-delay: -0.1s;
}

@keyframes anim2 {
  0% {
    transform: translate3d(1300px, -450px, 0) rotate(0deg);
    opacity: 1;
  }

  100% {
