@keyframes borderAnim {
  0% {
    width: 50%;
    height: 50%;
    border-color: rgba(230, 199, 131, 0);
  }

  100% {
    width: 100%;
    height: 100%;
    border-color: #e6c783;
  }
}

@keyframes beforeLine {
  0% {
    width: 0;
    transform: rotate(0deg);
  }

  100% {
    width: 95%;
    opacity: 1;
    transform: rotate(315deg);
  }
}

@keyframes rotateAnimation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes rotateAnimationHover {
  0% {
    transform: rotate(360deg);
  }

  100% {
    transform: rotate(0deg);
  }
}

@keyframes beforeLineHover {
  0% {
    width: 95%;
    opacity: 1;
    transform: rotate(360deg);
  }

  100% {
    width: 0;
    opacity: 0;
    transform: rotate(-45deg);
  }
}

@keyframes bgOpacity {
  0% {
    background-color: rgba(0, 0, 0, 0);
  }

  100% {
    background-color: black;
  }
}

@keyframes borderShow {
  0% {
    border-color: rgba(230, 199, 131, 0);
  }

  100% {
    border-color: #e6c783;
  }
}

.stages-work__item {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 50%;
  height: 50%;
  border: 2px solid rgba(230, 199, 131, 0);
  border-radius: 50%;
  font-weight: 300;
}

.stages-work__item:before {
  content: "";
  position: absolute;
  width: 0;
  height: 2px;
  background-color: #e6c783;
  transform: rotate(-45deg);
  opacity: 0;
}

.animation-start .stages-work__item::before {
  animation: 1.7s linear forwards beforeLine;
}

.animation-start .stages-work__item {
  animation: 1.8s linear forwards borderAnim;
}

.animation-start .stages-work__item .stages-work__item-text {
  animation: 1.7s linear forwards rotateAnimation;
}

.stages-work .border-hide-anim {
  border-color: rgba(0, 0, 0, 0);
}

.animation-start .border-hide-anim {
  animation: 8s ease forwards borderShow;
}

/* Hover stage */

.stage-hover-animation .stages-work__item-text {
  animation: 1.7s linear forwards rotateAnimationHover !important;
}

.stage-hover-animation::before {
  animation: 1.7s linear forwards beforeLineHover !important;
}

